.container {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  flex: 1 1 auto;
//  display: grid;
//  grid-template-columns: calc(306 / $rem-size) * 1rem 1fr;
//  grid-template-rows: auto;
}
.left-side {
  flex: 0 0 calc(306 / $rem-size) * 1rem;
  background-color: white;
  padding: calc(36 / $rem-size) * 1rem calc(28 / $rem-size) * 1rem calc(76 / $rem-size) * 1rem;
  display: flex;
  flex-direction: column;  
}
.close {
  display: none;
}
.logo-n-nav {
  flex: 1 1 auto;
}
.logo {
  margin-bottom: calc(45 / $rem-size) * 1rem;
  a {
    display: inline-flex;
    align-items: flex-end;
    justify-content: flex-start;
    color: $color-logo;
    text-decoration: none;
  }
  img {
    width: calc(195 / $rem-size) * 1rem;
    display: block;
  }
  span {
    font-size: calc(10 / $rem-size) * 1rem;
    line-height: calc((15 / 10) * 1em);
    font-weight: 500;
    margin-left: calc((4 / 10) * 1em);
    margin-bottom: .6em;
  }
}
.navigation {
  margin-bottom: calc(36 / $rem-size) * 1rem;
  &__menu {
    @include no-list;    
  }

  &__item:not(:last-child) {
    margin-bottom: calc(18 / $rem-size) * 1rem;
  }

  &__item {
    &--active.navigation__item {
      .navigation__link {
        background-color: $color-active;
        color: white;
        .navigation__icon, .navigation__arrow {
          .stroke {
            stroke: white;
          }
          .fill {
            fill: white;
          }
        }
      }
    }
  }

  &__link {
    display: block;
    padding: calc(13 / $rem-size) * 1rem calc(24 / $rem-size) * 1rem calc(12 / $rem-size) * 1rem calc(49 / $rem-size) * 1rem;
    color: $color-menu;
    text-decoration: none;
    position: relative;
    font-size: calc(14 / $rem-size) * 1rem;
    line-height: calc((21 / 14) * 1em);
    font-weight: 500;
    border-radius: calc(8 / $rem-size) * 1rem;
    &:hover {
      background-color: $color-paginabg;
      color: black;
      .navigation__icon, .navigation__arrow {
        .stroke {
          stroke: black;
        }
        .fill {
          fill: black;
        }
      }
    }
  }

  &__icon {
    position: absolute;
    left: calc(11 / $rem-size) * 1rem;
    top: calc(11 / $rem-size) * 1rem;
    width: 1.5rem;
    height: 1.5rem;
    svg {
      display: block;
      width: 100%;
    }
    .fill {
      fill: #9197B3;
    }
    .stroke {
      stroke: #9197B3;
    }
  }

  &__arrow {
    position: absolute;
    right: calc(8 / $rem-size) * 1rem;
    top: calc(15 / $rem-size) * 1rem;
    width: 1rem;
    height: 1rem;
    svg {
      display: block;
      width: 100%;
    }
  }
}
.user {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  &__avatar {
    flex: 0 0 calc(42 / $rem-size) * 1rem;
    height: calc(42 / $rem-size) * 1rem;
    overflow: hidden;
    border-radius: 50%;
    position: relative;
    margin-right: calc(12 / $rem-size) * 1rem;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;    
  }

  &__name {
    color: black;
    font-size: calc(14 / $rem-size) * 1rem;
    font-weight: 500;
    line-height: calc((21 / 14) * 1em);
  }

  &__post {
    color: $color-post;
    font-size: calc(12 / $rem-size) * 1rem;
    font-weight: normal;
    line-height: calc((18 / 12) * 1em);
  }
}
.burger {
  display: none;
}
.main {
  flex: 1 1 auto;
  padding-left: calc(71 / $rem-size) * 1rem;
  padding-top: calc(41 / $rem-size) * 1rem;
  padding-right: calc($padding-container / $rem-size) * 1rem;
  padding-bottom: calc(41 / $rem-size) * 1rem;
  min-width: 0;
  &__header {
    display: flex;
    flex-wrap: wrap;
  }
}
.title {
  margin-bottom: calc(51 / $rem-size) * 1rem;
  flex: 1 1 100%;
}
.rounded-wrap {
  background-color: white;
  border-radius: calc(30 / $rem-size) * 1rem;
  box-shadow: 0px calc(10 / $rem-size) * 1rem calc(60 / $rem-size) * 1rem rgba(226, 236, 249, 0.5);
}
.customers {
  padding: calc(30 / $rem-size) * 1rem 0 calc(40 / $rem-size) * 1rem;
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 calc(($padding-customers) / $rem-size) * 1rem;
    margin-bottom: calc(26 / $rem-size) * 1rem;
  }

  &__titletext {
    flex: 1 1 auto;
    padding-right: 1rem;
    display: flex;
    flex-direction: column;
  }

  &__search {
    margin-right: calc(14 / $rem-size) * 1rem;
    flex: 0 0 calc(216 / $rem-size) * 1rem;
  }

  &__title {
    margin-bottom: calc(7 / $rem-size) * 1rem;
  }

  &__status {
    font-size: calc(14 / $rem-size) * 1rem;
    line-height: calc((21 / 14) * 1em);
    color: $color-ok;
  }

  &__scroll-block {
    overflow: auto;
    width: 100%;
    padding: 0 calc(($padding-customers) / $rem-size) * 1rem 0 calc($padding-customers / $rem-size) * 1rem;
  }

  &__table {
    width: 100%;
    min-width: 750px;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: calc(30 / $rem-size) * 1rem;
    padding: 0 calc(($padding-customers) / $rem-size) * 1rem;
  }

  &__info {
    font-size: calc(14 / $rem-size) * 1rem;
    line-height: calc((21 / 14) * 1em);
    color: $color-textlight;
    font-weight: 500;
    margin-right: 1rem;
  }

  &__pagination {
    margin-bottom: 0;
    margin-right: calc($margin-pagination / $rem-size) * -1rem;
  }
}
.search {
  position: relative;
  img {
    position: absolute;
    left: calc(8 / $rem-size) * 1rem;
    top: 50%;
    transform: translateY(-50%);
    width: calc(24 / $rem-size) * 1rem;
  }
  input[type='text'] {
    display: block;
    line-height:1;
    width: 100%;
    box-sizing: border-box;
    font-family: inherit;
    height: calc(38 / $rem-size) * 1rem;
    padding: 0 .5rem 0 calc(40 / $rem-size) * 1rem;
    font-size: calc(12 / $rem-size) * 1rem;
    border-radius: calc(10 / $rem-size) * 1rem;
    color: inherit;
    background-color: $color-bginput;
    border: calc(1 / $rem-size) * 1rem solid $color-bginput;
  }
  @include placeholder($color-textlight,1);
}
.table {
  display: table;
  width: 100%;
  min-width: calc(820 / $rem-size) * 1rem;

  &__tr {
    display: table-row;
  }

  &__tr:first-child {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: calc($padding-customers / $rem-size) * -1rem;
      right: calc(($padding-customers) / $rem-size) * -1rem;
      bottom: 0;
      height: calc(1 / $rem-size) * 1rem;
      background-color: $color-border;
    }
  }

  &__cell {
    display: table-cell;
   &--th {
    padding: calc(14 / $rem-size) * 1rem 0;
    padding-right: calc(6 / $rem-size) * 1rem;
    font-size: calc(14 / $rem-size) * 1rem;
    line-height: calc((21 / 14) * 1em);
    color: $color-textlight;
    font-weight: 500;
    text-align: left;
   }

   &--name {
    //width: 19.36%;
    max-width: calc(151 / $rem-size) * 1rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    &:hover {
      overflow: visible;
      span {
        background-color: white;
        position: relative;
        padding: calc(4 / $rem-size) * 1rem;
        box-shadow: 0 0 calc(5 / $rem-size) * 1rem rgba(0,0,0,.5);
        transition: box-shadow .3s ease-in-out, padding .3s ease-in-out;
      }
    }
   }

   &--company {
    width: calc(131 / $rem-size) * 1rem;
   }

   &--phone {
    width: calc(156 / $rem-size) * 1rem;
   }

   &--email {
    max-width: 11.4375rem;
    //9.4375rem
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    &:hover {
      overflow: visible;
      span {
        background-color: white;
        position: relative;
        padding: calc(4 / $rem-size) * 1rem;
        box-shadow: 0 0 calc(5 / $rem-size) * 1rem rgba(0,0,0,.5);
        transition: box-shadow .3s ease-in-out, padding .3s ease-in-out;
      }
    }
   }

   &--country {
    width: calc(140 / $rem-size) * 1rem;
   }

   &--td {
    border-bottom: calc(1 / $rem-size) * 1rem solid $color-border;
    color: #292D32;
    font-size: calc(14 / $rem-size) * 1rem;
    line-height: calc((21 / 14) * 1em);
    font-weight: 500;
    vertical-align: middle;
    text-align: left;
    padding: calc(20 / $rem-size) * 1rem 0;
    padding-bottom: calc(19 / $rem-size) * 1rem;
    padding-right: calc(6 / $rem-size) * 1rem;
   }
   &--status {
    padding-right: calc(6 / $rem-size) * 1rem;
    text-align: center;
    width: calc(80 / $rem-size) * 1rem;
   }
  }
}
.label {
  display: inline-flex;
  width: calc(80 / $rem-size) * 1rem;
  height: calc(29 / $rem-size) * 1rem;
  border-radius: calc(4 / $rem-size) * 1rem;
  border: calc(1 / $rem-size) * 1rem solid transparent;
  font-size: calc(14 / $rem-size) * 1rem;
  line-height: calc((21 / 14) * 1em);
  font-weight: 500;
  justify-content: center;
  align-items: center;
  &--active {
    color: $color-oklabel;
    border-color: $color-bor-ok;
    background-color: $color-bg-ok;
  }

  &--inactive {
    color: $color-bor-err;
    border-color: $color-bor-err;
    background-color: $color-bg-err;
  }
}
.pagination {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  font-size: calc(12 / $rem-size) * 1rem;
  line-height: 1em;
  font-weight: 500;
  color: $color-paginatext;
  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(26 / $rem-size) * 1rem;
    height: calc(24 / $rem-size) * 1rem;
    border-radius: calc(4 / $rem-size) * 1rem;
    background-color: $color-paginabg;
    border: calc(1 / $rem-size) * 1rem solid $color-border;
    color: inherit;
    text-decoration: none;
    margin: 0 calc($margin-pagination / $rem-size) * 1rem;
    &--dots {
      background: none;
      border: none; 
      width: auto;     
    }
    &--active {
      background-color: $color-active;
      color: white;
    }
  }
}

@media screen and (max-width: $desk-sm-width) {
  :root {
    font-size: 13.7px;
  }
  .main {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
  .left-side {
    flex: 0 0 12rem;
    background-color: white;
    padding: calc(24 / $rem-size) * 1rem calc(16 / $rem-size) * 1rem calc(52 / $rem-size) * 1rem;
    display: flex;
    flex-direction: column;  
  }
  .logo {
    img {
      width: 9rem;
    }
    span {
      margin-bottom: .3em;
    }
  }
  .navigation__link {
    padding-top: 0.6rem;
    padding-bottom: 0.6rem;
  }
  .navigation__icon {
    top: 0.4rem;
  }
  .navigation__arrow {
    top: 0.7rem;
  }
}

@media screen and (max-width: $tab-width) {
  .main {
    padding: 1rem;
  }
  .logo {
    margin-bottom: 1rem;
  }
  .title {
    margin-bottom: 1rem;
  }
  .customers {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
  .customers__header, .customers__footer, .customers__scroll-block {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  .table__tr:first-child:after {
    left: -1rem;
    right: -1rem;
  }
  .left-side {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    overflow: auto;    
    z-index: 1000;
    width: 15rem;
    padding-top: 3rem;
    transform: translateX(-100%);
    transition: box-shadow .3s ease-in-out, transform .3s ease-in-out;
    &--active {
      transform: translateX(0%);
      box-shadow: 0 0 1rem rgba(0,0,0,.3);
    }
  }
  .scroll-lock {
    overflow: hidden;
  }
  .burger {
    width: 1.5rem;
    display: block;
    margin-right: 1rem;
    margin-top: calc(5 / $rem-size) * 1rem;
    img {
      display: block;
      width: 100%;
    }
  }
  .close {
    position: absolute;
    right: 1rem;
    top: 1rem;
    width: 1.5rem;
    display: block;
    img {
      display: block;
      width: 100%;
    }
  }
  .overlay {
    position: fixed;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
    background-color: rgba(0,0,0,0.2);
  }
}
@media screen and (max-width: $xs-mob-width) {
  .rounded-wrap {
    border-radius: 1rem;
  }
  .customers__header, .customers__footer {
    flex-wrap: wrap;
  }
  .customers__titletext, .customers__info {
    flex: 0 0 100%;
    order: 2;
  }
  .customers__search, .customers__pagination {
    flex: 0 0 100%;
    margin-right: 0;
    margin-bottom: 1rem;
    order: 1;
  }
  .customers__pagination {
    margin-left: calc(-6 / $rem-size) * 1rem;
  }
}